import React from 'react';
import s from './Post.module.css'
import {postsType} from "../../../../redux/store";


function Post(props: postsType) {
    return (
        <div className={s.item}>
            <img
                src='data:image/jpeg;base64,'/>
            {props.message}
            <div>
                <span>like</span> {props.likesCount}
            </div>
        </div>
    );
};

export default Post;